import {createEnhancedElement} from '@servicenow/library-enhanced-element';
import {makeRedispatchEffect} from '@servicenow/library-enhanced-utils';
import {Fragment} from '@servicenow/ui-renderer-snabbdom';
import styles from './_now-label-value-tabbed.scss';
import toExtension from './utils/to-extension';
import normalizeValues from './utils/normalize-values';
import renderValue from './utils/render-value';
import focusBehavior from '@servicenow/behavior-focus';
import {itemsSchema} from './schemas/label-value-schema';

const renderItem = ({label, value}, labelIndex, componentId, delimiter) => {
	const valueCount =
		Array.isArray(value) && value.length > 1 ? value.length : undefined;
	return (
		<Fragment>
			<dt role="listitem" id={`label-${componentId}-${labelIndex}`}>
				<div className="now-line-height-crop">
					<div className="now-label-value-label" title={label}>
						{label}
					</div>
				</div>
			</dt>
			<dd aria-labelledby={`label-${componentId}-${labelIndex}`}>
				{normalizeValues(value).map((v, i) =>
					renderValue(v, i, false, delimiter, valueCount)
				)}
			</dd>
		</Fragment>
	);
};

const view = (state) => {
	const {componentId} = state;
	const {items, size, delimiter} = state.properties;
	const sizeClasses = toExtension(size, ['sm', 'md']);
	return (
		<dl
			class={{
				['now-label-value']: true,
				['-tabbed']: true,
				['has-multiple']: items.length > 1,
				...sizeClasses
			}}>
			{items.map((item, i) => renderItem(item, i, componentId, delimiter))}
		</dl>
	);
};

/**
 * Label-value set showing the labels and values horizontally side-by-side.
 *
 * ```jsx
 * <now-label-value-tabbed
 * 	items={[
 * 		{
 * 			label: 'State',
 * 			value: {type: 'string', value: 'Closed', previous: 'Opened'}
 * 		},
 * 		{
 * 			label: 'Incident',
 * 			value: {
 * 				type: 'text-link',
 * 				label: 'INC0645648',
 * 				href: '#',
 * 				underlined: true
 * 			}
 * 		},
 * 		{
 * 			label: 'State',
 * 			value: {
 * 				type: 'highlighted-value',
 * 				label: 'In Progress',
 * 				status: 'positive',
 * 				showIcon: true
 * 			}
 * 		}
 * 	]}
 * />
 * ```
 *
 * @seismicElement now-label-value-tabbed
 * @summary  Method to scan and read label-values
 * in a list form with a solution for displaying multiple label-values for cases in which
 * horizontal space is limited.
 * @uib.label Label value tabbed
 * @uib.icon label-value-tabbed
 * @uib.description Method to scan and read label-values in a list form with a solution for displaying multiple label-values for cases in which horizontal space is limited.
 * @uib.category primitives
 * @uib.associatedTypes global.core
 * @uib.properties items, size, delimiter
 */
createEnhancedElement('now-label-value-tabbed', {
	properties: {
		/**
		 * Array of label-value items. Labels can have one or more values. Values
		 * can be rendered as a string, text link, highlighted value, or from raw
		 * HTML/JSX.
		 *
		 * Label/value items are configured with the following keys:
		 * - `label`: String. Text displayed as the label. Required.
		 * - `value`: String, Object, or Object[]. Single value or array of values. Required.
		 *
		 * Each `value` can be a string for a simple value or an object for more
		 * complex values. Object values are configured using the following keys:
		 *
		 * String type:
		 * - `type`: String. Renders value as a string.
		 * - `value`: String. Current value.
		 * - `previous`: String. Previous value (crossed out).
		 *
		 * highlighted-value type (see now-highlighted-value documentation for details):
		 * - `type`: String. Enter "highlighted-value" to render the value with now-highlighted-value.
		 * - `label`: String. Current value. Required.
		 * - `status`: String. Sets the background/icon color. Default value: "low".
		 * - `showIcon`: Boolean. When true, shows a solid circle icon next to the label. Default value: false.
		 *
		 * text-link type (see now-text-link documentation for details):
		 * - `type`: String. Enter "text-link" to render the value with now-text-link component.
		 * - `label`: String. Current value. Required.
		 * - `href`: String. URL opened when user clicks the link. Required.
		 * - `variant`: String. Sets the link color. Default value: "primary".
		 * - `underlined`: Boolean. When true, underlines the link. Default value: false.
		 * - `opensWindow`: Boolean. When true, opens the link in a new window or tab. Default value: false.
		 *
		 * jsx type:
		 * - `type`: String. Enter "jsx" to render the value as raw JSX.
		 * - `value`: JSX. JSX content. Required.
		 * - `previous`: JSX. Previous JSX content to track changes (crossed out).
		 *
		 * html type:
		 * - `type`: String. Enter "html" to render the value as raw HTML.
		 * - `value`: String. HTML content. Required.
		 * - `previous`: String. Previous HTML content to track changes (crossed out). Required.
		 *
		 * This component does not validate `jsx` or `html` content types for accuracy.
		 * Only use these content types when necessary. For example, when the content you are
		 * rendering is saved in the database as HTML and you must render it directly.
		 *
		 * @type {{label: string, values: (string|Object|Object[])}[]}
		 * @uib.description Array of label-value items. Labels can have one or more values. Values can be rendered as a string, text link, highlighted value, or from raw HTML. Avoid using HTML as values won't be able to be translated
		 * @uib.fieldType json
		 * @uib.defaultValue [{"label": "State", "value": {"type": "string", "value": "Closed", "previous": "Opened"}}, {"label": "Impact", "value": {"type": "string", "value": "1 - High"}}, {"label": "Category", "value": {"type": "string", "value": "Software"}}, {"label": "Assignment group", "value": [{"type": "string", "value": "Software"}, {"type": "text-link", "label": "Hardware", "href": "#", "underlined": true}, {"type": "string", "value": "Telephony"}, {"type": "highlighted-value", "label": "Routers"}, {"type": "string", "value": "Monitors"}]}, {"label": "Assigned to", "value": {"type": "string", "value": "Fred Luddy"}}]
		 */
		items: {
			default: [],
			schema: itemsSchema
		},
		/**
		 * Sets the label size and value text.
		 * @type {("sm"|"md")}
		 */
		size: {default: 'md', schema: {type: 'string', enum: ['sm', 'md']}},
		/**
		 * Character displayed as delimiter between multiple values associated with
		 * a label.
		 * @type {string}
		 * @uib.description Character displayed as delimiter between multiple values associated with a label.
		 */
		delimiter: {default: ',', schema: {type: 'string'}}
	},
	actionHandlers: {
		'NOW_TEXT_LINK#CLICKED': makeRedispatchEffect(
			'NOW_LABEL_VALUE_TABBED#TEXT_LINK_CLICKED'
		)
	},
	view,
	behaviors: [
		{
			behavior: focusBehavior
		}
	],
	styles,
	dispatches: {
		/**
		 * Dispatched when a label-value of type `text-link` is clicked.
		 * Payload contains the configuration object from the `items` array
		 * that corresponds to the clicked link. You can add keys to uniquely
		 * identify the link.
		 * @type {{item: Object}}
		 */
		'NOW_LABEL_VALUE_TABBED#TEXT_LINK_CLICKED': {}
	}
});
