import {createEnhancedElement} from '@servicenow/library-enhanced-element';
import {makeRedispatchEffect} from '@servicenow/library-enhanced-utils';
import styles from './_now-label-value-stacked.scss';
import toExtension from './utils/to-extension';
import normalizeValues from './utils/normalize-values';
import renderValue from './utils/render-value';
import focusBehavior from '@servicenow/behavior-focus';
import {itemsSchema} from './schemas/label-value-schema';

const renderItem = (
	{label, value},
	labelIndex,
	componentId,
	truncated,
	itemMinWidth,
	delimiter
) => {
	const valueCount =
		Array.isArray(value) && value.length > 1 ? value.length : undefined;
	return (
		<div
			className="now-label-value-pair"
			style={itemMinWidth ? {'min-width': itemMinWidth} : undefined}>
			<dt role="listitem" id={`label-${componentId}-${labelIndex}`}>
				<div className="now-line-height-crop">
					<div
						className="now-label-value-label"
						title={truncated ? label : undefined}>
						{label}
					</div>
				</div>
			</dt>
			<dd aria-labelledby={`label-${componentId}-${labelIndex}`}>
				{normalizeValues(value).map((v, i) =>
					renderValue(v, i, truncated, delimiter, valueCount)
				)}
			</dd>
		</div>
	);
};

const view = (state) => {
	const {componentId} = state;
	const {
		items,
		align,
		size,
		truncated,
		wrapped,
		itemMinWidth,
		delimiter
	} = state.properties;
	const alignClasses = toExtension(align, [
		'vertical',
		'horizontal',
		'horizontal-equal'
	]);
	const sizeClasses = toExtension(size, ['sm', 'md']);
	const customColumnRepeat = `repeat(auto-fit, minmax(${itemMinWidth}, 1fr))`;
	return (
		<dl
			style={
				itemMinWidth && wrapped
					? {'grid-template-columns': customColumnRepeat}
					: undefined
			}
			class={{
				['now-label-value']: true,
				['-stacked']: true,
				['will-truncate']: truncated,
				['will-wrap']: wrapped,
				...alignClasses,
				...sizeClasses
			}}>
			{items.map((item, i) =>
				renderItem(item, i, componentId, truncated, itemMinWidth, delimiter)
			)}
		</dl>
	);
};

/**
 * The stacked label-value component provides the following layout types:
 * - Vertical: Best for layout label-value in a column or if horizontal space is limited.
 * - Horizontal: Best for displaying label-value in columns or for associating label-values with a header or title.
 * - Horizontal-equal: Best if you have the predefined size of the label-value's parent container because it equally fills the container based on its size.
 *
 * ```jsx
 * <now-label-value-stacked
 * 	items={[
 * 		{
 * 			label: 'State',
 * 			value: {type: 'string', value: 'Closed', previous: 'Opened'}
 * 		},
 * 		{
 * 			label: 'Incident',
 * 			value: {
 * 				type: 'text-link',
 * 				label: 'INC0645648',
 * 				href: '#',
 * 				underlined: true
 * 			}
 * 		},
 * 		{
 * 			label: 'State',
 * 			value: {
 * 				type: 'highlighted-value',
 * 				label: 'In Progress',
 * 				status: 'positive',
 * 				showIcon: true
 * 			}
 * 		}
 * 	]}
 * />
 * ```
 *
 * @seismicElement now-label-value-stacked
 * @summary Label-value set showing the labels vertically above values or horizontally side-by-side.
 * @uib.label Label value stacked
 * @uib.icon label-value-stacked
 * @uib.description Label-value set showing the labels vertically above values or horizontally side-by-side.
 * @uib.category primitives
 * @uib.associatedTypes global.core
 * @uib.properties items, align, size, truncated, wrapped, itemMinWidth, delimiter
 */
createEnhancedElement('now-label-value-stacked', {
	properties: {
		/**
		 * Array of label-value items. Labels can have one or more values. Values
		 * can be rendered as a string, text link, highlighted value, or from raw
		 * HTML/JSX.
		 *
		 * Label/value items are configured with the following keys:
		 * - `label`: String. Text displayed as the label. Required.
		 * - `value`: String, Object, or Object[]. Single value or array of values. Required.
		 *
		 * Each `value` can be a string for a simple value or an object for more
		 * complex values. Object values are configured using the following keys:
		 *
		 * string type:
		 * - `type`: String. Renders value as a string.
		 * - `value`: String. Current value.
		 * - `previous`: String. Previous value (crossed out).
		 *
		 * highlighted-value type (see now-highlighted-value documentation for details):
		 * - `type`: String. Enter "highlighted-value" to render the value with now-highlighted-value.
		 * - `label`: String. Current value. Required.
		 * - `status`: String. Sets the background/icon color. Default value: "low".
		 * - `showIcon`: Boolean. When true, shows a solid circle icon next to the label. Default value: false.
		 *
		 * text-link type (see now-text-link documentation for details):
		 * - `type`: String. Enter "text-link" to render the value with now-text-link component.
		 * - `label`: String. Current value. Required.
		 * - `href`: String. URL opened when user clicks the link. Required.
		 * - `variant`: String. Sets the link color. Default value: "primary".
		 * - `underlined`: Boolean. When true, underlines the link. Default value: false.
		 * - `opensWindow`: Boolean. When true, opens the link in a new window or tab. Default value: false.
		 *
		 * jsx type:
		 * - `type`: String. Enter "jsx" to render the value as raw JSX.
		 * - `value`: JSX. JSX content. Required.
		 * - `previous`: JSX. Previous JSX content to track changes (crossed out).
		 *
		 * html type:
		 * - `type`: String. Enter "html" to render the value as raw HTML.
		 * - `value`: String. HTML content. Required.
		 * - `previous`: String. Previous HTML content to track changes (crossed out). Required.
		 *
		 * This component does not validate `jsx` or `html` content types for accuracy.
		 * Only use these content types when necessary. For example, when the content you are
		 * rendering is saved in the database as HTML and you must render it directly.
		 *
		 * @type {{label: string, values: (string|Object|Object[])}[]}
		 * @uib.description Array of label-value items. Labels can have one or more values. Values can be rendered as a string, text link, highlighted value, or from raw HTML. Avoid using HTML as the values won't be able to be translated
		 * @uib.fieldType json
		 * @uib.defaultValue [{"label": "State", "value": {"type": "string", "value": "Closed", "previous": "Opened"}}, {"label": "Impact", "value": {"type": "string", "value": "1 - High"}}, {"label": "Category", "value": {"type": "string", "value": "Software"}}, {"label": "Assignment group", "value": [{"type": "string", "value": "Software"}, {"type": "text-link", "label": "Hardware", "href": "#", "underlined": true}, {"type": "string", "value": "Telephony"}, {"type": "highlighted-value", "label": "Routers"}, {"type": "string", "value": "Monitors"}]}, {"label": "Assigned to", "value": {"type": "string", "value": "Fred Luddy"}}]
		 * @uib.typeMetadata.emptyStateBehavior EMPTY_ARRAY
		 */
		items: {
			default: [],
			schema: itemsSchema
		},
		/**
		 * Positions label/value items in relation to each other:
		 * - "vertical": Displays label-value pairs on top of each other.
		 * - "horizontal": Displays label-value pairs side-by-side.
		 * - "horizontal-equal": Displays label-value pairs side-by-side, expanding to equally fill its parent container.
		 * @type {("vertical"|"horizontal"|"horizontal-equal")}
		 * @uib.label Alignment
		 * @uib.description Positions label/value items in relation to each other: [vertical] - Displays label-value pairs on top of each other. [horizontal] - Displays label-value pairs side-by-side. [horizontal-equal] - Displays label-value pairs side-by-side, expanding to equally fill the parent container.
		 */
		align: {
			default: 'vertical',
			schema: {
				type: 'string',
				enum: ['vertical', 'horizontal', 'horizontal-equal']
			}
		},
		/**
		 * Sets the label size and value text.
		 * @type {("sm"|"md")}
		 */
		size: {default: 'md', schema: {type: 'string', enum: ['sm', 'md']}},
		/**
		 * When true, truncates labels and values that do not fit on a single
		 * line within the parent container. Has no effect on `html` or `jsx` items.
		 * @type {boolean}
		 * @uib.description Select to truncate labels and values that do not fit on a single line within the parent container
		 */
		truncated: {default: false, schema: {type: 'boolean'}},
		/**
		 * If `align` is set to "horizontal" or "horizontal-equal", label-value
		 * pairs display in a single row to fill the width available. Set this
		 * flag to cause the label-value pairs to wrap to multiple horizontal rows.
		 * @type {boolean}
		 * @uib.label Wrap text
		 * @uib.description If `align` is set to "horizontal" or "horizontal-equal", label-value pairs display in a single row to fill the width available. Set this flag to cause the label-value pairs to wrap to multiple horizontal rows.
		 */
		wrapped: {default: false, schema: {type: 'boolean'}},
		/**
		 * If `wrapped` or `truncated` is true, the default min-width of an
		 * individual label-value pair is 50px. Set this property to override the
		 * min-width of each item. Value must have units.
		 * @type {string}
		 * @uib.label Item min. width
		 * @uib.description  Set this property to override the min-width of each item. Value must have units
		 */
		itemMinWidth: {schema: {type: 'string'}},
		/**
		 * Character displayed as a delimiter between multiple values associated with
		 * a label.
		 * @type {string}
		 * @uib.description Character displayed as a delimiter between multiple values associated with a label
		 */
		delimiter: {default: ',', schema: {type: 'string'}}
	},
	actionHandlers: {
		'NOW_TEXT_LINK#CLICKED': makeRedispatchEffect(
			'NOW_LABEL_VALUE_STACKED#TEXT_LINK_CLICKED'
		)
	},
	view,
	behaviors: [
		{
			behavior: focusBehavior
		}
	],
	styles,
	dispatches: {
		/**
		 * Dispatched when a label-value of type `text-link` is clicked.
		 * Payload contains the configuration object from the `items` array
		 * that corresponds to the clicked link. You can add keys to uniquely
		 * identify the link.
		 * @type {{item: Object}}
		 * @uib.description Dispatched when a label-value of type `text-link` is clicked
		 */
		'NOW_LABEL_VALUE_STACKED#TEXT_LINK_CLICKED': {
			schema: {
				type: 'object',
				properties: {
					item: {type: 'object'}
				}
			}
		}
	}
});
