import {
	Fragment,
	dangerouslyCreateElementFromString
} from '@servicenow/ui-renderer-snabbdom';
import '@servicenow/now-highlighted-value';
import '@servicenow/now-text-link';
import {t} from 'sn-translate';

const _renderValueDelimiter = (i, delimiter, valueCount) => {
	if (i < valueCount - 1) {
		return (
			<span data-test="value-delimiter" aria-hidden="true">
				{delimiter}
			</span>
		);
	} else {
		return '';
	}
};

const _renderPreviousDelimiter = (
	<strong className="now-label-value-previous-delimiter">
		{t('{0} was {1}', '', '')}
	</strong>
);

const renderString = (
	{value, previous},
	i,
	truncated,
	delimiter,
	valueCount
) => {
	const previousTitle = t('{0} was {1}', value, previous);
	const previousContent = previous ? (
		<Fragment>
			{_renderPreviousDelimiter}
			<span className="now-label-value-previous">{previous}</span>
		</Fragment>
	) : (
		undefined
	);
	return (
		<div className="now-line-height-crop">
			<div
				className="now-label-value-value"
				title={truncated ? (previous ? previousTitle : value) : undefined}>
				{value}
				{previousContent}
				{_renderValueDelimiter(i, delimiter, valueCount)}
			</div>
		</div>
	);
};

const renderTextLink = (value, i, truncated, delimiter, valueCount) => (
	<div className="now-line-height-crop">
		<div className="now-label-value-value">
			<now-text-link
				title={truncated ? value.label : undefined}
				label={value.label}
				href={value.href}
				variant={value.variant}
				underlined={value.underlined}
				opens-window={value.opensWindow}
				append-to-payload={{item: value}}
			/>
			{_renderValueDelimiter(i, delimiter, valueCount)}
		</div>
	</div>
);

const renderHighlightedValue = (value, i, delimiter, valueCount) => (
	<div className="now-label-value-highlighted-value-parent">
		<now-highlighted-value
			className="now-label-value-highlighted-value"
			label={value.label}
			status={value.status}
			show-icon={value.showIcon}
		/>
		{_renderValueDelimiter(i, delimiter, valueCount)}
	</div>
);

const renderJSX = ({value, previous}) => {
	const previousContent = previous ? (
		<Fragment>
			{_renderPreviousDelimiter}
			<div className="now-label-value-previous">{previous}</div>
		</Fragment>
	) : (
		undefined
	);
	return (
		<div className="now-line-height-crop">
			<div className="now-label-value-value">
				{value}
				{previousContent}
			</div>
		</div>
	);
};

const renderHTML = ({value, previous}) => {
	const previousContent = previous ? (
		<Fragment>
			{_renderPreviousDelimiter}
			<div className="now-label-value-previous">
				{dangerouslyCreateElementFromString(previous)}
			</div>
		</Fragment>
	) : (
		undefined
	);
	return (
		<div className="now-line-height-crop">
			<div className="now-label-value-value">
				{dangerouslyCreateElementFromString(value)}
				{previousContent}
			</div>
		</div>
	);
};

export default function renderValue(
	value,
	i,
	truncated,
	delimiter,
	valueCount
) {
	if (value.type === 'string') {
		return renderString(value, i, truncated, delimiter, valueCount);
	}
	if (value.type === 'text-link') {
		return renderTextLink(value, i, truncated, delimiter, valueCount);
	}
	if (value.type === 'highlighted-value') {
		return renderHighlightedValue(value, i, delimiter, valueCount);
	}
	if (value.type === 'jsx') {
		return renderJSX(value);
	}
	if (value.type === 'html') {
		return renderHTML(value);
	}
}
