const valueSchema = {
	type: 'object',
	properties: {
		type: {
			type: 'string',
			enum: ['string', 'text-link', 'highlighted-value', 'jsx', 'html']
		},
		value: {
			oneOf: [{type: 'string', translatable: true}, {type: 'object'}]
		},
		previous: {type: ['string', 'object']},
		label: {type: 'string', translatable: true},
		status: {type: 'string'},
		showIcon: {type: 'boolean'},
		href: {type: 'string'},
		variant: {type: 'string'},
		underlined: {type: 'boolean'},
		opensWindow: {type: 'boolean'}
	}
};

export const itemsSchema = {
	type: 'array',
	items: {
		type: 'object',
		properties: {
			label: {type: 'string', translatable: true},
			value: {
				oneOf: [
					{type: 'string', translatable: true},
					valueSchema,
					{type: 'array', items: valueSchema}
				]
			}
		}
	}
};
