import {createEnhancedElement} from '@servicenow/library-enhanced-element';
import styles from './_now-label-value-inline.scss';

const view = (state) => {
	const {componentId} = state;
	const {label, value, reversed, truncated} = state.properties;
	if (!reversed) {
		return (
			<div class={{'now-label-value-inline': true, 'will-truncate': truncated}}>
				<div className="now-line-height-crop">
					<div
						className="now-label-value-pair"
						title={truncated ? `${label} ${value}` : undefined}>
						<span id={`label-${componentId}`}>{label}</span>
						&nbsp;
						<span role="definition" aria-labelledby={`label-${componentId}`}>
							{value}
						</span>
					</div>
				</div>
			</div>
		);
	} else {
		return (
			<div class={{'now-label-value-inline': true, 'will-truncate': truncated}}>
				<div className="now-line-height-crop">
					<div
						className="now-label-value-pair"
						title={truncated ? `${value} ${label}` : undefined}>
						<span role="definition" aria-labelledby={`label-${componentId}`}>
							{value}
						</span>
						&nbsp;
						<span id={`label-${componentId}`}>{label}</span>
					</div>
				</div>
			</div>
		);
	}
};

/**
 * The `now-label-value-inline` component is an independent instance of the label-value pairs
 * component that only displays horizontally. Pass settings into `now-label-value-inline` to format the label for your use case.
 *
 * ```jsx
 * <now-label-value-inline label="Assigned to" value="Fred Luddy" />
 * ```
 *
 * @seismicElement now-label-value-inline
 * @summary Single label-value pair shown inline with minimal formatting.
 * @uib.label Label value
 * @uib.icon label-value-inline
 * @uib.description Single label-value pair that displays inline horizontally with minimal formatting.
 * @uib.category primitives
 * @uib.associatedTypes global.core
 * @uib.properties label, value, reversed, truncated
 */
createEnhancedElement('now-label-value-inline', {
	properties: {
		/**
		 * Label text displaying inline with the value. Content is associated with the value, for example, "Assigned to".
		 * @type {string}
		 * @uib.description Label text displaying inline with the value
		 * @uib.defaultValue Label
		 * @uib.translatable true
		 */
		label: {schema: {type: 'string'}},
		/**
		 * Value text displayed inline with the label. Content is a unique value, for example, "Fred Luddy".
		 * @type {string}
		 * @uib.description Value text displayed inline with the label
		 * @uib.defaultValue This is the value
		 * @uib.translatable true
		 */
		value: {schema: {type: 'string'}},
		/**
		 * When true, reverses the label and value order.
		 * @type {boolean}
		 * @uib.description Set to reverse the label and value order
		 */
		reversed: {default: false, schema: {type: 'boolean'}},
		/**
		 * When true, truncates output to a single line within the width of the provided layout.
		 * @type {boolean}
		 * @uib.description Set to truncate output to a single line within the width of the provided layout
		 */
		truncated: {default: false, reflect: true, schema: {type: 'boolean'}}
	},
	view,
	styles
});
